import { defineComponent } from "vue";
import { request } from "./request";
import { localCache } from "./utils/cache";
export default defineComponent({
  setup: function setup() {
    request.get({
      url: "https://api.pofiart.com/api/support/ip2geo"
    }).then(function (res) {
      if (res.code === 200) {
        var result = res.data;

        if (result.region === "日本") {
          localCache.setCache("lang", "ja");
        } else if (result.region === "中国") {
          if (result.province === "香港" || result.province === "澳门" || result.province === "台湾") {
            localCache.setCache("lang", "zhTw");
          } else {
            localCache.setCache("lang", "en");
          }
        } else {
          localCache.setCache("lang", "en");
        }
      }
    });
    return {};
  }
});